import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './Weather.scss';

const weatherKey = '7bec195acf79e266f65c8bf808b61263'

export default function AirQuality({ location }) {

   const [weather, setWeather] = useState()
   const [time, setTime] = useState()
   const [errorMessage, setErrorMessage] = useState()

   useEffect(() => {
      const apiCallInterval = setInterval(() => {
         setTime(new Date())
      }, (1000 * 60 * 60))
      getWeather(location.latitude, location.longitude)
      return () => clearInterval(apiCallInterval)
   }, [location, time])
   function getWeather(lat, lon) {
      return axios.get(`http://api.openweathermap.org/data/2.5/forecast`, {
         params: { lat, lon, units: 'metric', lang: 'pl', APPID: weatherKey }
      }).then(res => {
         setWeather({
            temp: res.data.list[0].main.temp,
            description: res.data.list[0].weather[0].description,
            icon: res.data.list[0].weather[0].icon
         })
      }).catch(err => { setErrorMessage(err.message) })
   }

   return (
      weather && (
         <div className='weather__container'>
            <div className='weather__temperature'>{Math.round(weather.temp)}&#176;</div>
            <div className='weather__aura'>
               <img src={`http://openweathermap.org/img/wn/${weather.icon}@2x.png`} />
               <div className='weather__description'>  {weather.description}</div>
            </div>
         </div>
      ) || <div></div>
   )
}

